import React from 'react'

import CodeIcon from '@material-ui/icons/Code';
import BrushIcon from '@material-ui/icons/Brush';
import SchoolIcon from '@material-ui/icons/School';

import "./styles.scss";

const AboutContainer = () => {
    return (
      <div className="about-wrap">
          <div className="item">
            <div className="icon-wrap"><CodeIcon className="icon" /></div>  
            <div className="text-wrap">
              <p className="text">Frontend Developer</p>
              <p className="text-desc">Enthusiast Front-end coder love to code with clean, efficient and simple logic.</p>
            </div>
          </div>
          <div className="item">
            <div className="icon-wrap"><BrushIcon className="icon" /></div> 
            <div className="text-wrap">
              <p className="text">UI / UX Designer</p>
              <p className="text-desc">
                Passionate UI / UX designer
                with an aim to convert imagination into reality.
              </p>
            </div> 
          </div>
          <div className="item">
            <div className="icon-wrap"><SchoolIcon className="icon" /></div>  
            <div className="text-wrap">
              <p className="text">B.TECH (CSE)</p>
              <p className="text-desc">Bachelor of Technology in Computer Science and Engineering</p>
            </div>
          </div>
      </div>
    );
}

export default AboutContainer;
