import React from 'react';
import { PropTypes } from 'prop-types';

import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import GitHubIcon from '@material-ui/icons/GitHub';
import YouTubeIcon from '@material-ui/icons/YouTube';

// Styles
import "./styles.scss"

const useStyles = makeStyles({
  root: {
    maxWidth: 280,
  },
  media: {
    height: 140,
  },
  youTubeIcon: {
    fontSize: '45px',
    color: 'red !important',
  },
  gitHubIcon: {
    fontSize: '32px',
  },
  projectAccess: {
    marginTop: '15px',
    display: 'flex',
    alignItems: 'center',

    '& a': {
      textDecoration: 'none',
      color: 'black',
      display: 'inline-block',
      marginRight: '10px',
    },
  },
  heading: {
    fontWeight: '600',
    fontSize: '18px',
    paddingBottom: '10px',
    borderBottom: '1px solid #b7b7b7',
  },
  headingSmall: {
    marginTop: '15px',
    fontWeight: '600',
    fontSize: '16px',
  },
  techUsed: {
    '&::after': {
      display: 'inline-block',
      content: '" "',
      margin: '0 10px',
      width: '1px',
      height: '15px',
      backgroundColor: '#969696',
      verticalAlign: 'middle',
    },

    '&:last-of-type': {
      '&::after': {
        display: 'none'
      },
    },
  },
  cardImgWrap: {
    minWidth: '280px',
    height: '170px',

    '& img': {
      width: '100%',
      height: '100%',
      objectFit: 'cover',      
    },
  },
  profileImage: {
    objectFit: 'contain !important',
  },
  disableGitHub: {
    color: '#9d9d9d !important',
  },
});

const CardComponent = (props) => {
  const { image, extraClass, title, githubLink, youtubeLink, techUsed } = props;
  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <div className={classes.cardImgWrap}>
        <img src={image} alt="Restaurant" className={classes[extraClass]} />
      </div>
      <CardContent>
        <Typography component="p" className={classes.heading} >
          { title }
        </Typography>
        <div className="text-wrap">
          <Typography component="p" className={classes.headingSmall}>
            Technology Used:
          </Typography>
          <Typography component="p">
            {
              techUsed.map((tech) => {
                return (
                  <span className={classes.techUsed}>{ tech }</span>
                );
              })
            }
          </Typography>
        </div>
        <div className={classes.projectAccess}>
          <a href={githubLink} rel="noreferrer" target="_blank">
            <GitHubIcon className={`${classes.gitHubIcon} ${!githubLink && classes.disableGitHub}`} />
          </a>
          <a href={youtubeLink} rel="noreferrer" target="_blank">
            <YouTubeIcon className={classes.youTubeIcon} />
          </a>
        </div>
      </CardContent>
    </Card>
  );
}

CardComponent.propTypes = {
  image: PropTypes.string,
  extraClass: PropTypes.string,
  title: PropTypes.string,
  githubLink: PropTypes.string,
  youtubeLink: PropTypes.string,
  techUsed: PropTypes.arrayOf(PropTypes.string)
};

CardComponent.defaultProps = {
  image: '',
  extraClass: '',
  title: '',
  githubLink: '',
  youtubeLink: '',
  techUsed: [],
};

export default CardComponent;
