import React, { Fragment } from 'react'
import { Typography } from '@material-ui/core';
import GitHubIcon from '@material-ui/icons/GitHub';
import YouTubeIcon from '@material-ui/icons/YouTube';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TwitterIcon from '@material-ui/icons/Twitter';

// Styles
import './styles.scss';

const ConnectContainer = () => {
    return (
        <Fragment>
            <Typography className="main-title">
                " I love to work until imagination changes into reality "
            </Typography>
            <div className="connect-container">
                <div className="item-wrap">
                    <Typography className="small-title">
                        Like my work:
                    </Typography>
                    <div className="icon-wrap">
                        <a href="https://github.com/avas27" rel="noreferrer" target="_blank">
                            <GitHubIcon className="github" />
                        </a>
                        <a href="https://www.youtube.com/channel/UCfqU1zw2n7_dzWskNOep-Ww" rel="noreferrer" target="_blank">
                            <YouTubeIcon className="youtube" />
                        </a>
                    </div>
                </div>
                <div className="item-wrap">
                    <Typography className="small-title">
                        Connet with me:
                    </Typography>
                    <div className="icon-wrap">
                        <a href="https://www.linkedin.com/in/abhishekvermasn27/" rel="noreferrer" target="_blank">
                            <LinkedInIcon className="linkedin" />
                        </a>
                        <a href="https://twitter.com/avas27avas" rel="noreferrer" target="_blank">
                            <TwitterIcon className="twitter" />
                        </a>
                    </div>
                </div>
            </div>
        </Fragment>
    );
};

export default ConnectContainer;
